.progress-circle {
    width: 150px;
    height: 150px;
    line-height: 150px;
    background: none;
    margin: 0 auto;
    -webkit-box-shadow: none;
            box-shadow: none;
    position: relative;
}
.progress-circle:after{
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 2px solid #fff;
    position: absolute;
    top: 0;
    left: 0;
}
.progress-circle > span{
    width: 50%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    z-index: 1;
}
.progress-circle .progress-left{
    left: 0;
}
.progress-circle .progress-circle-bar{
    width: 100%;
    height: 100%;
    background: none;
    border-width: 2px;
    border-style: solid;
    position: absolute;
    top: 0;
}
.progress-circle .progress-left .progress-circle-bar{
    left: 100%;
    border-top-right-radius: 80px;
    border-bottom-right-radius: 80px;
    border-left: 0;
    -webkit-transform-origin: center left;
    transform-origin: center left;
}
.progress-circle .progress-right{
    right: 0;
}
.progress-circle .progress-right .progress-circle-bar{
    left: -100%;
    border-top-left-radius: 80px;
    border-bottom-left-radius: 80px;
    border-right: 0;
    -webkit-transform-origin: center right;
    transform-origin: center right;
    -webkit-animation: loading-1 1.8s linear forwards;
            animation: loading-1 1.8s linear forwards;
}
.progress-circle .progress-value{
    width: 85%;
    height: 85%;
    border-radius: 50%;
    border: 2px solid #ebebeb;
    font-size: 32px;
    line-height: 125px;
    text-align: center;
    position: absolute;
    top: 7.5%;
    left: 7.5%;
}
.progress-circle.blue .progress-circle-bar{
    border-color: #049dff;
}
.progress-circle.blue .progress-value{
    color: #049dff;
}
.progress-circle.blue .progress-left .progress-circle-bar{
    -webkit-animation: loading-2 0.4s linear forwards 1.8s;
            animation: loading-2 0.4s linear forwards 1.8s;
}
.progress-circle.brown .progress-circle-bar{
    border-color: brown;
}
.progress-circle.brown .progress-value{
    color: brown;
}
.progress-circle.brown .progress-left .progress-circle-bar{
    -webkit-animation: loading-3 1s linear forwards 1.8s;
            animation: loading-3 1s linear forwards 1.8s;
}
.progress-circle.orange .progress-circle-bar{
    border-color: #FF530D;
}
.progress-circle.orange .progress-value{
    color: #FF530D;
}
.progress-circle.orange .progress-left .progress-circle-bar{
    -webkit-animation: loading-4 0.4s linear forwards 1.8s;
            animation: loading-4 0.4s linear forwards 1.8s;
}
.progress-circle.green .progress-circle-bar{
    border-color: #1abc9c;
}
.progress-circle.green .progress-value{
    color: #1abc9c;
}
.progress-circle.green .progress-left .progress-circle-bar{
    -webkit-animation: loading-5 1.2s linear forwards 1.8s;
            animation: loading-5 1.2s linear forwards 1.8s;
}
@-webkit-keyframes loading-1{
0%{
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
}
100%{
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
}
}
@keyframes loading-1{
0%{
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
}
100%{
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
}
}
